*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
  }
.home {
    -webkit-flex-grow:1;
            flex-grow:1;
    min-width: 100;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.home .title{
    line-height:50px
}
.home .title button{
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 24px;
    margin-top: 30px;
    color: aliceblue;
    padding:10px 15px;
    border-radius: 999px;
    background-color: blue;
    font-family: 'Syne Mono', monospace;
}
.home{
    width:100%;
    margin-top: 20px;
}
.home .person{
    width:80%;
}

.wrapper{
  display: -webkit-flex;
  display: flex;
}
.wrapper .static-txt{
  color: #fff;
  font-size: 60px;
  font-weight: 400;
}
.wrapper .dynamic-txts{
  margin-left: 15px;
  height: 90px;
  line-height: 90px;
  overflow: hidden;
}
.dynamic-txts li{
  list-style: none;
  color: #3010be;
  font-size: 60px;
  font-weight: 500;
  position: relative;
  top: 0;
  -webkit-animation: slide 12s steps(4) infinite;
          animation: slide 12s steps(4) infinite;
}
@-webkit-keyframes slide {
  100%{
    top: -360px;
  }
}
@keyframes slide {
  100%{
    top: -360px;
  }
}
.dynamic-txts li span{
  position: relative;
  margin: 5px 0;
  line-height: 90px;
}
.dynamic-txts li span::after{
  content: "";
  position: absolute;
  left: 0;
  height: 100%;
  width: 100%;
  background: #18458a;
  border-left: 2px solid #6dfc9d;
  -webkit-animation: typing 3s steps(10) infinite;
          animation: typing 3s steps(10) infinite;
}
@-webkit-keyframes typing {
  40%, 60%{
    left: calc(100% + 30px);
  }
  100%{
    left: 0;
  }
}
@keyframes typing {
  40%, 60%{
    left: calc(100% + 30px);
  }
  100%{
    left: 0;
  }
}
