footer {
    width: 100%;
    padding: 10px 0;
  }
  
  footer img {
    max-width: 60px;
  }
  
  footer img:not(:last-child) {
    margin-right: 10px;
  }
.navbar {
    height: 100%;
  }
  
  .nav-wrapper {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  
  .logo {
    display: -webkit-flex;
    display: flex;
    max-width: 130px;
    height: 100%;
  }
  
  .logo img {
    width: 100%;
    margin-left: -18px;
  }
  
  .list-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .list-wrapper ul {
    top: 0;
    left: 0;
    z-index: 3;
    width: 100%;
    height: 86vh;
    left: -100vw;
    padding-top: 140px;
    text-align: center;
    position: absolute;
    transition: all 0.5s ease-in-out;
    background-color: rgb(0, 0, 0, 0.8);
  }
  
  .list-wrapper ul li {
    list-style: none;
    margin-bottom: 15px;
  }
  
  .list-wrapper ul li a {
    display: block;
    font-size: 24px;
    padding: 10px 0;
    color: #dddddd;
    text-decoration: none;
    transition: all 0.3s ease-in-out;
  }
  
  .list-wrapper ul li a:hover {
    color: #000000;
    background-color: #dddddd;
  }
  
  .list-wrapper img {
    opacity: 0;
    z-index: 4;
    width: 40px;
    right: 15px;
    padding: 8px;
    position: absolute;
    border-radius: 999px;
    background-color: #dddddd;
    transition: all 0.5s ease-in-out;
  }
  
  @media (min-width: 800px) {
    .list-wrapper img {
      display: none;
    }
  
    .list-wrapper ul {
      padding: 0;
      width: 100%;
      height: 100%;
      text-align: left;
      position: relative;
      background-color: transparent;
    }
  
    .list-wrapper ul li {
      display: inline-block;
      margin-bottom: 0;
    }
  
    .list-wrapper ul li:not(:last-child) {
      margin-right: 10px;
    }
  
    .list-wrapper ul li a {
      padding: 5px;
      color: #000000;
    }
  
    .list-wrapper ul li a:hover {
      padding: 5px;
      color: #333333;
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
      background: transparent;
    }
  }
